<template>
	<view class="sweep-ring-code">
		<!-- 相机自定义扫码 -->
		<xh-scan-code ref="xhScanCode" @onScancode="onScancode" :openNow="false" />
		<!-- 中奖弹窗 -->
		<xh-winning-window ref="winningWindow" @closeNotice="closeNotice" />
		<!-- 中奖弹窗二 -->
		<win-again ref="winAgain" @closeNotice="closeNotice"/>
		<!-- 中奖弹窗三 -->
		<win-again-three ref="winAgainThree" @closeNotice="closeNotice"/>
		<!-- 中奖第四次弹窗 -->
		<win-again-four  ref="winAgainFour" @closeNotice="closeNotice"/>
		<!-- 未中奖弹窗 -->
		<xh-msg-dialog ref="xhMsgDialog" @closeNotice="closeNotice" @goWxScan="goWxScan"/>
		<!-- 提示背景 -->
		<cover-view class="scan-tips" v-show="isShowTips">
			<cover-view class="scan-tips-num">扫拉环二维码</cover-view>
			<cover-view class="scan-tips-text">
				<cover-view>扫中国红牛拉环二维码</cover-view>
				<cover-image class="scan-tips-logo" src="../static/sweep-ring-code-icon.png"></cover-image>
			</cover-view>
		</cover-view>
		<!-- 遇到问题？ -->
		<cover-view v-if="isShowTips" class="have-problem" @click="haveProblem">
			<cover-view class="have-problem-text">扫码问题</cover-view><cover-image  class="have-problem-icon" src="../static/tips_red.png"></cover-image>
		</cover-view>
	</view>
</template>

<script>
	import {
		mapGetters
	} from 'vuex'
	import {
		cscanQr
	} from '@/api/homeApi.js'
	/*消息提示*/
	import {xhAudio} from '@/utils/index.js'
	import {fileBaseUrl} from '@/utils/http.js'
	import xhWinningWindow from './xh-winning-window.vue'
	import winAgain from './winAgain'
	import winAgainThree from './winAgainThree'
	import winAgainFour from './winAgainFour.vue'
	import {
		removeWxScanQrCode,
		getScanSweepNum,
		setScanSweepNum
	} from '@/utils/auth.js'
	//scanCode
	let _currCode = ''
	//音频控制
    let error_audio=null,success_audio=null,swept_audio = null
	//连续中奖累计
	let _number = 0
	let _wxScanQrCode = ''
	export default {
		onLoad(o) {
			//接收传参
			_wxScanQrCode =  o.wxScanQrCode
			//次数清零
			_number = getScanSweepNum()
			//请扫中国红牛拉环二维码
			error_audio = xhAudio({
				 url:'https://file.y1b.cn/public/music/hnlh.mp3'
			 })
			 this.initSuccessAudio(_number)
			//您已领取过该奖券
			 swept_audio = xhAudio({
				 url:fileBaseUrl + '/public/img/to2/cmp3/02/BFXN_MUSIC_04.mp3'
			 })
		},
		computed: {
			...mapGetters(['userLocation'])
		},
		data(){
			return {
				isShowTips:true,
				isHaveProblem:false
			}
		},
		onShow() {
			//接受页面参数
			_currCode = ''
			
			//处理页面参数
			setTimeout(()=>{
				if(_wxScanQrCode){
					//先关闭扫码页
					_currCode = _wxScanQrCode
					this.closeScan()
					//处理传参结果
					this.winningCheck(_wxScanQrCode)
					//置空
					_wxScanQrCode = ''
				}else{
					this.showScan()
				}
			},0)
		},
		components:{
			xhWinningWindow,
			winAgain,
			winAgainThree,
			winAgainFour
		},
		methods: {
			initSuccessAudio(number){
				let index = number>=4?1:(number+1)
				 //恭喜您中奖了
				success_audio = xhAudio({
					 url:'https://file.y1b.cn/public/music/bfxl/202105/win_0'+(index)+'.mp3'
				 })
			},
			haveProblem(){
				this.isHaveProblem = true
				//关闭
				this.$refs.xhScanCode.close() 
				this.$refs.xhMsgDialog.show({msg:'扫码遇到问题？黑屏或相机无法打开？请点击确认按钮，为您提供中转方案~'},'goWxScan')
			},
			goWxScan(){
				//调取系统扫码
				wx.scanCode({
				  success:(res)=>{
					  this.onScancode(res.result)
				  }
				})
			},
			onScancode(scanCode) {
				// console.log('scanCode ----->onScancode', scanCode)
				//有值说明还在处理，先暂停
				if (_currCode) return
				_currCode = scanCode//存储当前值
				this.$refs.xhScanCode.play() //播放音效
				//关闭扫码背景
				this.closeScan()
				setTimeout(()=>{
					//查询是否中奖
					this.winningCheck(_currCode)
				},0)
			},
			closeScan(){
				this.isShowTips = false
				this.$refs.xhScanCode.close() 
			},
			showScan(){
				this.isShowTips = true
				if(!this.isHaveProblem)this.$refs.xhScanCode.reset() 
			},
			winningCheck(scanCode) {
				//拉环扫码中奖查询
				cscanQr({
					code: _currCode,
					...this.userLocation
				}).then(res => {
					_wxScanQrCode = ""
					//删除微信扫码进来的
					removeWxScanQrCode()
                    if (res.code == 1) {
						_number++//累加次数
						if(_number>4) _number = 1//防止下标越界
                    	//音效
                    	success_audio.play()
						//再次中奖
						switch(_number){
							case 1://第一中奖
							   this.$refs.winningWindow.show(res.data)
							break 
							case 2://第二次中奖
							   this.$refs.winAgain.show(res.data)
							break 
							case 3://第三次中奖
							   this.$refs.winAgainThree.show(res.data)
							break 
							case 4://第三次中奖
							   this.$refs.winAgainFour.show(res.data)
							break 					
						}
						this.initSuccessAudio(_number)
						setScanSweepNum(_number)
						return
                    }
                    //中奖卡劵
                    if(res.code == 2){
						_number = 0//中断连续
						setScanSweepNum(0)
                    	this.$router.navigateTo({
                    		url:'/pages/personal/scanResult/index?type=2&data='+encodeURIComponent(JSON.stringify(res.data))
                    	})
                    	return 
                    }
                    //中奖积分
                    if(res.code == 3){
						_number = 0//中断连续
						setScanSweepNum(0)
                    	this.$router.navigateTo({
                    		url:'/pages/personal/scanResult/index?type=1&data='+encodeURIComponent(JSON.stringify(res.data))
                    	})
                    	return 
                    }
                    //其它情况
                    this.$refs.xhMsgDialog.show(res)
                    //非红牛拉环二维码
                    if(res.data&&res.data.code == 3)error_audio.play()
                    //已扫
                    if(res.data&&res.data.code == 10)swept_audio.play()

				}).catch(err => {
					setTimeout(function() {
						_currCode = ''
					}, 3000)
				})
			},
			closeNotice() {
				_currCode = ''
				this.showScan()
			}
		},
		onUnload() {
			error_audio.destroy()
			success_audio.destroy()
		}
	}
</script>

<style lang="scss">
	.sweep-ring-code {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		background-color: #333;
		.scan-tips{
			position: fixed;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 348rpx;
			background-color: #44924F;
		}
		.scan-tips-text{
			display: flex;
			justify-content: center;
			align-items: center;
			color: #FFFFFF;
			font-size: 40rpx;
			margin-top: 128rpx;
		}
		.scan-tips-logo{
			width: 150rpx;
			height: 125rpx;
			margin-left: 28rpx;
		}
		.scan-tips-num{
			color: #FFFFFF;
			position: absolute;
			top: 20rpx;
			left: 0;
			width: 100%;
			text-align: center;
		}
		.have-problem{
			width: 168rpx;
			height: 50rpx;
			padding-left: 24rpx;
			padding-right: 10rpx;
			box-sizing: border-box;
			background-color: #FFE4E1;
			position: fixed;
			bottom: 265rpx;
			right: 0;
			border-radius: 15px 0 0 15px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			z-index: 1;
		}
		.have-problem-text{
			font-size: 24rpx;
			color: #FE2821;
		}
		.have-problem-icon{
			width: 28rpx;
			height: 28rpx;
			margin-left: 10rpx;
		}
	}
</style>
